






















import menuContent from "./menu_content.vue";
import channelDetail from './channel_detail.vue'
import Vue from "vue";

export enum Tab{
    "Top",
    "Communities"
}
let MenuStatus: boolean = false;
export class TabController{
    
    public static CurrentTab = Tab.Top
    public static VueObj
    public static Minus(){
        if(TabController.CurrentTab > 0){
            TabController.CurrentTab -= 1
            TabController.Refresh()
        }
    }
    public static Plus(){
        if(TabController.CurrentTab < 1){
            TabController.CurrentTab += 1
            TabController.Refresh()
        }
    }
    public static SetTabTo(tab: Tab){
        TabController.CurrentTab = tab
        TabController.Refresh()
    }
    private static Refresh(){
        TabController.VueObj.$set(TabController.VueObj.tab, 'value', TabController.CurrentTab)
    }
}


class Vector2{
    public X: number;
    public Y: number;
    constructor(X: number, Y: number){
        this.X = X
        this.Y = Y
    }
}
let touchstart_Pos: Vector2;
window.addEventListener("touchstart", function(event){
    if(event.changedTouches.length != 0 && MenuStatus){
        touchstart_Pos = new Vector2(event.changedTouches[0].screenX, event.changedTouches[0].screenY)
        console.log(touchstart_Pos)
    }
})
const menuDiv = document.getElementById("menu-content")
window.addEventListener("touchmove", function (event) {
    var touch = event.touches[0];
    if(touchstart_Pos != null){
        // Sync
        //menuDiv.style.transform = "translateX(" + touch.clientX + "px)"
        console.log(touch.clientX)
    }
});
window.addEventListener("touchend", function(event){
    if(event.changedTouches.length != 0 && touchstart_Pos != null && MenuStatus){
        const touchend_Pos = new Vector2(event.changedTouches[0].screenX, event.changedTouches[0].screenY)
        if(touchend_Pos.X - touchstart_Pos.X > 70){
            TabController.Minus()
        }
        else if(touchend_Pos.X - touchstart_Pos.X < -70){
            TabController.Plus()
        }
        else{
            
            console.log("OG")
        }
    }
    touchstart_Pos = null
})
var timer = 0;
window.onresize = function () {
  if (timer > 0) {
    clearTimeout(timer);
  }
 
  timer = setTimeout(function () {
        if(window.parent.screen.width > 600)
          MenuStatus = true
  }, 200);
};

export default {
    name: "headermenu",
    updated: function(){
        if(window.parent.screen.width > 600)
          MenuStatus = true
        TabController.VueObj = this
        document.onkeydown = function(e) {
        if((e.shiftKey && !e.metaKey) && MenuStatus){
            if(e.keyCode == 37){
                TabController.Minus()
            }
            if(e.keyCode == 39){
                TabController.Plus()
            }
        }
        }
    },
    data: function () {
        return  {
            tab: {value: Tab.Top}
        }
    },
    methods: {
        setMenuStatus: function(status: boolean){
            // If menu is opened, the status will true
            MenuStatus = status
        }
    },
    created: function(){
        TabController.VueObj = this
        document.onkeydown = function(e) {
        if((e.shiftKey && !e.metaKey)){
            if(e.keyCode == 37){
                TabController.Minus()
            }
            if(e.keyCode == 39){
                TabController.Plus()
            }
            //thisobj.tab.splice();
        }
}
    },
    components: {
        "menu-content": menuContent,
        "channel-detail": channelDetail
    }
}
