import Vue from "vue";
import VueRouter from "vue-router";
import home from "@/views/Home.vue";
import animation_view from "@/views/animation_view.vue"
import animation_analyzer from "@/assets/ts/animation_analyzer"
import NotFound from "@/components/page_general/not_found_error.vue"
import * as ChannelDetail from "@/components/header/channel_detail.vue"
Vue.use(VueRouter);

const animData = new animation_analyzer(document.URL)
const routes = [
  {
    path: "/",
    name: "home",
    component: home
  },
  {
    path: "/anim/" + animData.AnimID,
    name: "anim",
    component: animation_view
  },
  {
    path: "/anim/" + animData.AnimID + "&" + animData.ThreadID,
    name: "anim",
    component: animation_view
  },
  {
    path: '*',
    component: NotFound
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
