
























import Vue from 'vue'
import VueRouter from 'vue-router'
import loader from '@/components/system/loading.vue'
import URLAnalyzer from '@/assets/ts/animation_analyzer'
import * as Talk from '@/assets/data_converter/talk'
import UserData from '@/assets/data_converter/userdata'
import $ from 'jquery'
import * as Header from '@/components/header/header_menu.vue'

function delayedCall(second: number, callBack: TimerHandler){
  setTimeout(callBack, second * 1000);
}

function scrollToLast(){
  var contents = document.getElementsByClassName('baloon')
  contents[contents.length - 1].scrollIntoView(true)
}

let talk: Talk.default;
export default {
  data: function () {
    return {
      loading_status: {value: true},
      channel: "general",
      recent_talk: [],
      UserID: UserData.LoginedUser,
      textInput: ""
    }
  },
  created: function(){
    Header.TabController.SetTabTo(Header.Tab.Communities)
    //Header.TabController.SetTabTo(Header.Tab.Communities)
    this.recentTalk()
    var obj = this
    document.onkeydown = function(e) {
      if((e.ctrlKey && !e.metaKey) || (!e.ctrlKey && e.metaKey))
        if(e.key == "Enter")
          obj.post()
    }
    var elem1 = document.getElementById('inputField');
    console.log($('textarea'))
    $('textarea')
    .focusin(function(e) {
      alert("IN")
    })
    .focusout(function(e) {
      alert("OUT")
    });
    },
  methods: {
    recentTalk: function(){
      const analyzer = new URLAnalyzer(document.URL)
      talk = new Talk.default(analyzer.AnimID, analyzer.ThreadID)
      talk.RecentTalks[0].UserID
      this.recent_talk = talk.RecentTalks
      // 再描画
      this.recent_talk.splice();
    },
    post: function(){
      const newPost = talk.Post(new Talk.TalkData("TestUser", this.textInput))
      this.recent_talk = newPost
      this.textInput = ""
      // 再描画
      this.recent_talk.splice()
      Vue.nextTick()
        .then(function () {
            scrollToLast()
        })
    }
  },
  components: {
      'loader': loader
  },
  mounted: function(){
    // Off the loading
    delayedCall(2, (time) =>{
      const backelement = document.getElementById("loading")
      backelement.style.backgroundColor = "rgb(255, 255, 255)"
      delayedCall(.3, (time) =>{
      this.$set(this.loading_status, 'value', false)
      })
      if(this.recent_talk.length != 0)
        scrollToLast()
    })
  }
}
