export class WikiElement{
    constructor(type: string, targetClass: string, data: KeyValuePair[]){
        this.type = type
        this.targetClass = targetClass
        this.data = data
    }
    private type: string
    private targetClass: string
    private data: KeyValuePair[]

    public get Type() : string {
        return this.type
    }
    
    public get TargetClass() : string {
        return this.targetClass
    }
    
    public get Data() : KeyValuePair[] {
        return this.data
    }
}

export class KeyValuePair{
    constructor(key: string, value: any){
        this.key = key
        this.value = value
    }
    private key: string;
    private value: any;
}