














import AnimationAnalyzer from "@/assets/ts/animation_analyzer"
import AnimarionData from "@/assets/data_converter/animation_data"
import notfound from "@/components/page_general/anim_notfounderror.vue"
import community from "@/components/animations/anim_community.vue"

export default {
    data: function(){
        return {

        }
    },
    methods: {
        isExists: function(): boolean{
            const anim = new AnimationAnalyzer(document.URL)
            if(!anim.IsAnimURL)
                return false
            else
            {
                const animation_data = new AnimarionData().GetAnimation(anim.AnimID)
                if(animation_data == null)
                    return false
                else
                    return true
            }
        }
    },
    components: {
        "not-found-error": notfound,
        "community": community
    }
}
