





































import URLAnalyzer from '@/assets/ts/animation_analyzer'
import * as AnimationData from '@/assets/data_converter/animation_data'
import Animation_analyzer from '@/assets/ts/animation_analyzer';


class AnimData{
    private static animationData: AnimationData.Animation | null;
    private static AnalyzeResult: URLAnalyzer;
    public static get GetAnalyzeResult() : URLAnalyzer {
        if(AnimData.AnalyzeResult == undefined)
            AnimData.AnalyzeResult = new URLAnalyzer(document.URL)
        return AnimData.AnalyzeResult
    }
    public static set SetAnalyzeResult(analyzer: URLAnalyzer){
        this.AnalyzeResult = analyzer
    }
    public static get GetAnimationData() : AnimationData.Animation {
        if(AnimData.animationData == null || AnimData.animationData ==  undefined){
            if(AnimData.GetAnalyzeResult.IsAnimURL)
                AnimData.animationData = new AnimationData.default().GetAnimation(AnimData.AnalyzeResult.AnimID)
        }
        return AnimData.animationData
    }
    
}


export default {
    methods: {
        IsAvailable: function(){
            const analyzed = new Animation_analyzer(document.URL)
            return analyzed.IsAnimURL && analyzed.AnimID != null
        },
        GetAnimationData: function(){
            return AnimData.GetAnimationData
        },
        LinkTo: function(command: string){
            const result = new URLAnalyzer(document.URL)
            if(result.ThreadID != command){
                this.$router.push(AnimData.GetAnalyzeResult.AnimID + "&" + command)
                window.location.reload();
            }
        },
        CreateNewThread: function(){
            alert("Create!")
        }
    }

}
