










function delayedCall(second: number, callBack: TimerHandler){
  setTimeout(callBack, second * 1000);
}
export default {
  data: function () {
    return {
        loading_status: {value: false}
    }
    }
  ,
  methods: {
    
  },
  components: {
  },
  beforeMount: function(){
    delayedCall(2, (time) =>{
      this.$set(this.loading_status, 'value', true)
      })
  }
}
