import animData from "@/assets/data_converter/animation_data"

export default class Talk{
    constructor(animID: string, channelID: string){
        // Get recent talkdata from rails server
        // This is a test data
        const testData = new TestDataset()
        if(animID == testData.animID && channelID == testData.channelID){
            this.recent_talks = testData.talks
            this.all_talks =  testData.talks
        }
    }
    private recent_talks: TalkData[]
    private all_talks: TalkData[]
    
    public get RecentTalks() : TalkData[] {
        return this.recent_talks
    }
    public Post(data: TalkData){
        if(this.recent_talks.length > 20)
            this.recent_talks.shift()
        this.recent_talks.push(data)
        return this.recent_talks
    }
    
}

export class TalkData{
    constructor(userID: string, content: string, attachment_image: Blob[] | null = null){
        this.userID = userID
        this.content = content
        this.attachment_image = attachment_image
    }
    private userID: string;
    private content: string;
    private attachment_image: Blob[] | null;
    
    public get UserID() : string {
        return this.userID
    }
    
    public get Content() : string {
        return this.content
    }

    public get Images() : Blob[] | null {
        return this.attachment_image
    }
}

