










import userDialog from "./user_dialog.vue"

export default {
    name: "DBInteg",
    data: function () {
        return  {
          userDialogStatus: false
        }
    },
    methods: {
      toggleUserDialog: function(){
        var maskel = document.getElementById("mask");
        var style = maskel.style;
        if(this.userDialogStatus){
          this.userDialogStatus = false
          style.display = "none"
        }
       else{
          this.userDialogStatus = true
          style.display = "inherit"
        }
      }
    },
    mounted: function(){
    },
    components: {
        "user-dialog": userDialog
    }
}
