



















import AnimationData from "@/assets/data_converter/animation_data"
import * as Header from '@/components/header/header_menu.vue'

export default {
  data: function () {
    return {
    }
  },
  methods: {
      animations: function(){
          return new AnimationData().Animations
      },
      loadAnimation: function(ID: string){
