export default class Animation_analyzer{
    public RootURL: string;
    public AnimID: string;
    public IsAnimURL: boolean;
    public ThreadID: string;
    constructor(URL: string){
        if(URL.includes("anim")){
            this.IsAnimURL = true
            this.RootURL = URL.substr(0, URL.lastIndexOf("anim/") + "anim".length + 1);
            this.AnimID = URL.substr(URL.lastIndexOf("anim/") + "anim/".length, 36)
            if(URL.includes("&"))
                this.ThreadID = URL.substr(URL.lastIndexOf("&") + 1)
        }
    else{
        this.IsAnimURL = false
        this.RootURL = URL
        this.AnimID = null
    }
}
}